.custom-control.material-checkbox {
  --color: #26a69a; }
  .custom-control.material-checkbox .material-control-input {
    display: none; }
    .custom-control.material-checkbox .material-control-input:checked ~ .material-control-indicator {
      border-color: var(--color);
      -webkit-transform: rotateZ(45deg) translate(1px, -5px);
      transform: rotateZ(45deg) translate(1px, -5px);
      width: 10px;
      border-top: 0px solid #fff;
      border-left: 0px solid #fff; }
  .custom-control.material-checkbox .material-control-indicator {
    display: inline-block;
    position: absolute;
    top: 4px;
    left: 0;
    width: 16px;
    height: 16px;
    border: 2px solid #aaa;
    -webkit-transition: .1s;
    transition: .1s; }

.custom-control.fill-checkbox {
  --color: #f8f9fa;
  margin: 0 0 0 0;
  display: inline-block; }
  .custom-control.fill-checkbox .fill-control-input {
    display: none; }
    .custom-control.fill-checkbox .fill-control-input:checked ~ .fill-control-indicator {
      background-color: var(--color);
      border-color: #aaa;
      background-size: 80%; }
  .custom-control.fill-checkbox .fill-control-indicator {
    border-radius: 3px;
    display: inline-block;
    position: absolute;
    top: 2px;
    left: 0;
    width: 16px;
    height: 16px;
    border: 1px solid #aaa;
    -webkit-transition: .1s;
    transition: .1s;
    background: transparent;
    background-size: 0%;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3E%3Cpath fill='%23aaa' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3E%3C/svg%3E"); }

.custom-control.overflow-checkbox .overflow-control-input {
  display: none; }
  .custom-control.overflow-checkbox .overflow-control-input:checked ~ .overflow-control-indicator::after {
    -webkit-transform: rotateZ(45deg) scale(1);
    transform: rotateZ(45deg) scale(1);
    top: -6px;
    left: 5px; }
  .custom-control.overflow-checkbox .overflow-control-input:checked ~ .overflow-control-indicator::before {
    opacity: 1; }

.custom-control.overflow-checkbox .overflow-control-indicator {
  border-radius: 3px;
  display: inline-block;
  position: absolute;
  top: 4px;
  left: 0;
  width: 16px;
  height: 16px;
  border: 2px solid #aaa; }
  .custom-control.overflow-checkbox .overflow-control-indicator::after {
    content: '';
    display: block;
    position: absolute;
    width: 16px;
    height: 16px;
    -webkit-transition: .3s;
    transition: .3s;
    -webkit-transform: rotateZ(90deg) scale(0);
    transform: rotateZ(90deg) scale(0);
    width: 10px;
    border-bottom: 4px solid #aaa;
    border-right: 4px solid #aaa;
    border-radius: 3px;
    top: -2px;
    left: 2px; }
  .custom-control.overflow-checkbox .overflow-control-indicator::before {
    content: '';
    display: block;
    position: absolute;
    width: 16px;
    height: 16px;
    -webkit-transition: .3s;
    transition: .3s;
    width: 10px;
    border-right: 7px solid #fff;
    border-radius: 3px;
    -webkit-transform: rotateZ(45deg) scale(1);
    transform: rotateZ(45deg) scale(1);
    top: -4px;
    left: 5px;
    opacity: 0; }

