/*
  * Basic styles, good for a large display. Everything fits in
  * one row, no wrapping. All text based cells grow equally.
  */
.table-row-wrapper {
  border-bottom: 1px solid #e2e7eb;
  cursor: pointer; }
  .table-row-wrapper:hover {
    background-color: #f9f9f9; }
  .table-row-wrapper.selected {
    background-color: #ebf5ff; }
  .table-row-wrapper .table-row {
    display: flex;
    display: -webkit-flex;
    flex-direction: row;
    flex-wrap: no-wrap;
    -webkit-flex-wrap: no-wrap;
    width: 100%;
    color: #242a30; }

.wrapper {
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  flex-grow: 0;
  -webkit-flex-grow: 0; }

.text-4, .text-2, .text {
  flex-grow: 1;
