/*
  * Media queries: optimize for different screen widths.
  */
@media all and (max-width: 640px) {
  .text-2, .num-2 {
    flex-direction: column; }
    .text-2 div, .num-2 div {
      flex-grow: 0;
      -webkit-flex-grow: 0;
      width: 100%; }
  .text-2 {
    width: 180px; }
  .num-2 {
    width: 80px; } }

